.button {
    position: relative;

    display: block;

    margin: 50px auto 0;
    padding: 0 12px;

    height: 32px;

    color: #686868;
    background: #fff;

    text-align: center;
    font-size: 14px;

    border-radius: 5px;
    border: 2px solid #c3c2c9;
    cursor: pointer;
    overflow: hidden;
}

a.button {
    line-height: 30px;
    height: 30px;
}