html {
    height: 100%;
    width: 100%;

    color: #333;

    font-family: "PT Mono", "Operator Mono Lig", "Fira Code", "Source Code Pro", "DejaVu Sans Mono", "Anonymous Pro", "Ubuntu Mono", "Roboto Mono", "JetBrains Mono", "Droid Sans Mono", Mono, Monaco, Menlo, Inconsolata, "Liberation Mono", ui-monospace, monospace, "Microsoft YaHei UI", system-ui, -apple-system, "system-ui", sans-serif, iconfont, icomoon, FontAwesome, "Font Awesome 5 Pro", "Font Awesome 6 Pro", IcoFont, fontello, themify, "Material Icons", "Material Icons Extended", bootstrap-icons, "Segoe Fluent Icons", Material-Design-Iconic-Font, office365icons, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji", "Android Emoji", EmojiSymbols, "emojione mozilla", "twemoji mozilla", "Segoe UI Emoji", "Segoe UI Symbol", Symbola, EmojiSymbols;

    overflow-x: hidden;
}

body {
    margin: 0;
    padding: 0;
    height: 100%;
    overflow: hidden;
}

body,
h1,
h2,
h3,
h4 {
    margin: 0;
    padding: 0;
    font-weight: normal;
}

button {
    font-family: "PT Mono", "Operator Mono Lig", "Fira Code", "Source Code Pro", "DejaVu Sans Mono", "Anonymous Pro", "Ubuntu Mono", "Roboto Mono", "JetBrains Mono", "Droid Sans Mono", Mono, Monaco, Menlo, Inconsolata, "Liberation Mono", ui-monospace, monospace, "Microsoft YaHei UI", system-ui, -apple-system, "system-ui", sans-serif, iconfont, icomoon, FontAwesome, "Font Awesome 5 Pro", "Font Awesome 6 Pro", IcoFont, fontello, themify, "Material Icons", "Material Icons Extended", bootstrap-icons, "Segoe Fluent Icons", Material-Design-Iconic-Font, office365icons, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji", "Android Emoji", EmojiSymbols, "emojione mozilla", "twemoji mozilla", "Segoe UI Emoji", "Segoe UI Symbol", Symbola, EmojiSymbols;
    appearance: none;
    cursor: pointer;
    outline: none;
    border: none;
}

a {
    text-decoration: none;
    cursor: pointer;
}
  