#user {
    text-align: center;
}
#user > a {
    display: inline-block;
}
#user img {
    padding: 4px;
    border: 1px solid #eaeaea;
    display: inline-block;
    width: 100px;
    height: 100px;
    margin: 70px auto 0;
    border-radius: 50%;
}
@media (max-width: 800px) {
    #user img {
        width: 75px;
        height: 75px;
        margin-top: 40px;
   }
}
#user h1 {
    font-size: 26px;
    margin-top: 20px;
    color: #42505a;
    font-weight: 600;
}
@media (max-width: 800px) {
    #user h1 {
        font-size: 20px;
   }
}
#user p {
    color: #5e656b;
    font-size: 16px;
    line-height: 1.4;
    margin: 14px 20px 0;
}
@media (max-width: 800px) {
    #user p {
        font-size: 14px;
   }
}
#user .social a {
    display: inline-block;
    margin: 20px 8px 30px;
}
@media (max-width: 800px) {
    #user .social a {
        margin: 16px 6px 20px;
   }
}
#user .social a:hover path {
    fill: #363a42;
}
#user svg {
    display: block;
    width: 22px;
    height: 22px;
}
@media (max-width: 800px) {
    #user svg {
        width: 18px;
        height: 18px;
   }
}
#user path {
    fill: #565b65;
}
#user .social:after {
    display: block;
    margin: 0 auto;
    width: 50%;
    content: '';
    height: 1px;
    background: #f1f1f1;
}
