.comment-list:before {
    display: block;
    width: 30%;
    content: '';
    height: 1px;
    background: #f1f1f1;
    margin: 50px auto;
}
.comment-list li {
    list-style: none;
    margin-top: 20px;
    position: relative;
    padding: 10px 0 10px 70px;
}
@media (max-width: 800px) {
    .comment-list li {
        padding-left: 60px;
   }
}
.comment-list .comment-body {
    border: 1px solid #dee1e2;
    padding: 14px 14px 14px 18px;
    border-radius: 3px;
    position: relative;
}
.comment-list .comment-body > a {
    color: #333;
    text-decoration: none;
    font-weight: 700;
}
.comment-list .comment-body span {
    font-size: 14px;
    color: #767676;
}
.comment-list .comment-body:before {
    content: '';
    position: absolute;
    border: 1px solid transparent;
    border-top-color: #dee1e2;
    border-left-color: #dee1e2;
    width: 5px;
    height: 5px;
    background-color: #fff;
    left: -5px;
    top: 20px;
    transform: rotate(-45deg);
}
.comment-list .markdown-body {
    margin-top: 10px;
    font-size: 14px;
    white-space: pre-wrap;
}
.comment-list .author {
    border: 1px solid #eee;
    position: absolute;
    left: 0;
    top: 10px;
    width: 48px;
    height: 48px;
    border-radius: 50%;
    overflow: hidden;
}
@media (max-width: 800px) {
    .comment-list .author {
        width: 36px;
        height: 36px;
   }
}
.comment-list .author img {
    display: block;
    width: 100%;
    height: auto;
}
@media (max-width: 800px) {
    .comment-body:before {
        top: 16px !important;
   }
}
