.page {
    position: fixed;
    top: 0;
    left: 0;

    width: 100%;
    height: 100%;

    background-color: #fff;

    transform: translate3d(0, 0, 0);
    overflow-x: hidden;
    overflow-y: scroll;
}

.page.scroll {
    overflow-y: scroll;
    -webkit-overflow-scrolling: touch;
}

.page-current {
    z-index: 1;
}

.page-movefrom {
    overflow: hidden;
    animation: movefrom .4s ease-in-out both;
}

.page-moveto {
    overflow: hidden;
    animation: moveto .5s ease-in both;
}

@keyframes movefrom {
    from {
        transform: translate3d(300px, 0, 0);
        opacity: 0;
    }
}

@keyframes moveto {
    to {
        transform: translate3d(-100%, 0, 0);
    }
}
