@import url(./global.css);
@import url(./transition.css);
@import url(./posts.css);
@import url(./post.css);
@import url(./memos.css);
@import url(./user.css);
@import url(./footer.css);
@import url(./swtheme.css);

.page > div {
    margin: 0 auto;
    max-width: 700px;
    padding: 0 20px;
}

.loading body:before {
    display: none;
}

#bar {
    z-index: 5;
    position: absolute;
    box-sizing: border-box;
    border-right: 10px solid #fff;
    border-left: 10px solid #fff;
    top: 0;
    left: 0;
    width: 100%;
    height: 3px;
    background: #3f4d56;
    transition: all 1s ease;
}
