
#memos ol {
    margin-bottom: 0;
    padding-bottom: 0;
    list-style: none;
}

#memos .timelines {
    display: flex;
    flex-direction: column;

    padding: 32px 0 0 32px;

    font-size: 14px;

    border-left: 2px solid #e3e3e3;
}

#memos .timeline {
    margin-bottom: 48px;
}

#memos .timeline:last-child {
    margin-bottom: 0;
}

#memos .timeline + .extra-space {
    margin-top: 48px;
}

#memos .timeline-icon {
    position: absolute;

    display: flex;
    align-items: center;
    justify-content: center;
    flex-shrink: 0;

    margin-left: -52px;

    width: 40px;
    height: 40px;

    overflow: hidden;
    border-radius: 50%;
    box-shadow: 0 0 0 6px #fff;
}

#memos .timeline-description {
    display: flex;
    align-items: center;

    padding-top: 11px;

    gap: 8px;
    color: #7b7b7b;
}

#memos .timeline-description svg {
    flex-shrink: 0;

    width: 16px;
    height: 16px;
}

#memos .timeline-icon {
    color: #fff;
    background-color: #195bf7;
    font-size: 17px;
}

#memos .timeline-icon.note-icon {
    background-color: #688afd;
}

#memos .timeline-icon.emoji-icon {
    background-color: #537188;
}

#memos .timeline-icon.image-icon {
    background-color: #E76161;
}

#memos .timeline-icon.morning-icon {
    background-color: #FAF8F1;
}

#memos .timeline-icon.noon-icon {
    background-color: #FFCD1E;
}

#memos .timeline-icon.night-icon {
    background-color: #2C3639;
}

#memos .timeline-icon.idea-icon {
    background-color: #749F82;
}

#memos .timeline-icon.think-icon {
    background-color: #FAF8F1;
}

#memos .timeline-icon.extract-icon {
    background-color: #0D4C92;
}

#memos .timeline-icon.coffee-icon {
    background-color: #663300;
}

#memos .timeline-icon.date-icon {
    background-color: #1B9C85;
    color: #fff;
}

#memos .comment {
    margin-top: 12px;
    padding: 16px;

    color: #3d3d3d;

    font-size: 14px;
    line-height: 29px;

    border: 1px solid #e3e3e3;
    border-radius: 6px;
    box-shadow: 0 4px 4px 0 var(--c-grey-100);
}

#memos .comment ol {
    list-style: initial;
}

/**
 * Mobile media
 */
@media (pointer: coarse) {

    /**
    * iPad pro 12 and below
    */
    @media only screen and (max-device-width: 1024px) {
        
    }

    /**
    * iPhone XS Max and below
    */
    @media only screen and (max-device-width: 414px) {

        #memos .timeline-description svg {
            width: 12px;
            height: 12px;
        }

        #memos .comment {
            padding: 12px;
            font-size: 14px;
        }
        
    }

    /**
    * iPhone SE and below
    */
    @media only screen and (max-device-width: 320px) {
        
    }
}