.darkmode {
    position: fixed;
    top: 10px;
    right: 20px;
    z-index: 20000;
}

.darkmode * {
    box-sizing: border-box;
}

.checkbox {
    opacity: 0;
    position: absolute;
}

.checkbox-label {
    position: relative;

    display: flex;
    justify-content: space-between;
    align-items: center;

    padding: 5px;

    width: 50px;
    height: 26px;

    background-color: #111;

    border-radius: 50px;
    cursor: pointer;
}

.checkbox-label .ball {
    position: absolute;
    top: 5px;
    left: 5px;

    width: 15px;
    height: 15px;

    background-color: #fff;

    border-radius: 50%;
    transition: transform 0.2s linear;
}

.checkbox:checked + .checkbox-label .ball {
    transform: translateX(24px);
}
