@import url(./paging.css);

#posts {
    text-align: center;
}

#posts > .post {
    text-align: left;
    cursor: pointer;
    position: relative;
    padding: 20px 10px;
    display: block;
    text-decoration: none;
}

#posts > .post:first-child {
    margin-top: 50px;
}

@media (max-width: 800px) {
    #posts > .post:first-child {
        margin-top: 30px;
   }
}

#posts > .post:hover {
    background: #f6f7f7;
}

#posts > .post div {
    position: absolute;
    right: 10px;
    top: 23px;
}

@media (max-width: 800px) {
    #posts > .post div {
        display: none;
   }
}

#posts > .post span {
    display: inline-block;

    margin: 0 3px;
    padding: 1px 3px 2px;

    color: #565b65;
    background: #e8e8e8;

    height: 19px;
    line-height: 21px;

    font-size: 13px;
    border: 1px solid #e1e1e1;
    border-radius: 3px;
}

#posts > .button {
    display: inline-block;

    margin-right: 5px;
    margin-left: 5px;

    text-align: center;
}

#posts p {
    font-size: 14px;
    color: #788590;
    margin: 14px 0 0 1px;
}

#posts .empty {
    margin-top: 100px;
}

@media (max-width: 800px) {
    #posts p {
        font-size: 13px;
   }
}

#posts h2 {
    color: #485763;
    font-size: 16px;
    line-height: 1.4;
    font-weight: 500;
    width: 60%;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}

.filter {
    display: flex;
    flex-direction: row;
    align-items: center;

    padding: 20px 10px;

    color: #788590;

    line-height: 1.4;

    font-size: 15px;
    font-weight: 500;
    text-overflow: ellipsis;
    white-space: nowrap;
}

.filter .home {
    display: flex;
    flex-direction: row;
    align-items: center;

    cursor: pointer;
}

.filter .split {
    margin: 0 5px;
}

@media (max-width: 800px) {
    #posts h2 {
        width: 100%;
        font-size: 15px;
    }

    .filter {
        font-size: 13px;
    }

    #posts .empty {
        margin-top: 50px;
   }
}
