@import url(./markdown.css);
@import url(./comments.css);

#post > p {
    font-size: 14px;
    color: #788590;
    margin-top: 10px;
}
#post > p span {
    margin: 0 10px;
    color: #565f67;
}
#post .labels {
    margin-top: 30px;
}
#post .labels a {
    margin-right: 10px;
    text-decoration: underline;
    font-size: 14px;
    color: #666;
}
#post > .markdown-body {
    margin-top: 40px;
}
#post > .markdown-body:before {
    content: '';
    width: 30px;
    display: block;
    margin-bottom: 20px;
    height: 1px;
    background: #eee;
}

#post .back {
    position: absolute;
    right: 50%;
    margin-right: 370px;
    top: 72px;
    width: 30px;
    height: 30px;
    cursor: pointer;
}
@media (max-width: 800px) {
    #post .back {
        top: 20px;
        left: 20px;
   }
}
#post .back:hover path {
    fill: #5c646b;
}
#post .back path {
    fill: #949fa9;
}
#post > h1 {
    font-size: 24px;
    font-weight: 500;
    line-height: 1.4;
    color: #3d4348;
    padding-top: 70px;
}
@media (max-width: 800px) {
    #post > h1 {
        font-size: 20px;
   }
}
